import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import axios from "axios";
import useAuth from "hooks/useAuth";
import useConstants from "constants";

const AuthMiddleware = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(null);
  const { BASE_URL } = useAuth();
  const [loading, setLoading] = useState(true);
  const { AUTH_URL } = useConstants();

  // Helper functions to manage tokens
  const checkAuth = async () => {
    try {
      const response = await axios.get(`${BASE_URL}/get-authentication/`, {
        withCredentials: true,
        params: {
          platform: "ulm",
        },
      });
      localStorage.setItem("logged_user", response.data.logged_user);
      setIsAuthenticated(true);
    } catch (error) {
      console.error("Error checking authentication:", error);
      setIsAuthenticated(false);
    } finally {
      setLoading(false);
    }
  };

  // Run token validation on mount
  useEffect(() => {
    checkAuth();
  }, []);

  // Handle authentication state
  if (isAuthenticated === null) {
    return <div>Loading...</div>;
  }

  if (!isAuthenticated) {
    window.location.assign(`${AUTH_URL}/sign-in`);
  }

  return children;
};

AuthMiddleware.propTypes = {
  children: PropTypes.node.isRequired,
};

export default AuthMiddleware;
