import useAuth from "hooks/useAuth";
import Card from "@mui/material/Card";
import MDBox from "components/MDBox";
import MDButton from "components/MDButton";
import { NavLink } from "react-router-dom";
import PageLayout from "examples/LayoutContainers/PageLayout";
import { Grid, Stack } from "@mui/material";
import useConstants from "constants";
import MDTypography from "components/MDTypography";
import { t } from "i18next";
import image from "assets/images/bg-img.webp";

const Dashboard = () => {
  const logged_user = localStorage.getItem("logged_user", "");
  const { ADMIN_URL, HUMAN_URL } = useConstants();

  const nextUrl = () => {
    if (logged_user == "admin") {
      return ADMIN_URL;
    } else if (logged_user == "human") {
      return HUMAN_URL;
    } else {
      return "/";
    }
  };

  const goto = () => {
    const url = nextUrl();
    window.location.assign(url);
  };

  const { logout } = useAuth();
  return (
    <PageLayout>
      <MDBox
        position="absolute"
        width="100%"
        minHeight="100vh"
        sx={{
          backgroundImage: ({ functions: { linearGradient, rgba }, palette: { gradients } }) =>
            image &&
            `${linearGradient(
              rgba(gradients.light.main, 0),
              rgba(gradients.light.state, 0)
            )}, url(${image})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
        }}
      />
      <MDBox px={1} width="100%" height="100vh" mx="auto">
        <Grid container spacing={1} justifyContent="center" alignItems="center" height="100%">
          <Grid item xs={11} sm={9} md={5} lg={6} xl={5}>
            <Card>
              <MDBox
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
                mx={2}
                mt={-3}
                p={2}
                mb={1}
                textAlign="center"
              >
                <MDTypography variant="h4" fontWeight="medium" color="white" mt={1}>
                  {t("lang.already_logged_in")}
                </MDTypography>
              </MDBox>
              <MDBox p={15}>
                <Stack direction="column" spacing={5} sx={{ justifyContent: "center" }}>
                  <MDButton type="submit" variant="gradient" color="info" onClick={goto}>
                    Go to {logged_user} Dashboard
                  </MDButton>
                  <MDButton type="submit" variant="gradient" color="error" onClick={logout}>
                    Logout
                  </MDButton>
                </Stack>
              </MDBox>
            </Card>
          </Grid>
        </Grid>
      </MDBox>
    </PageLayout>
  );
};

export default Dashboard;
