import React, { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import PropTypes from "prop-types";
import axios from "axios";
import useAuth from "hooks/useAuth";
import FullScreenLoader from "../components/FullScreenLoader";

const NoAuthMiddleware = ({ children }) => {
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(null);
  const { BASE_URL } = useAuth();

  const checkAuth = async () => {
    try {
      const response = await axios.get(`${BASE_URL}/get-authentication/`, {
        withCredentials: true,
        params: {
          platform: "ulm",
        },
      });
      setIsAuthenticated(true);
    } catch (error) {
      console.error("Error checking authentication:", error);
      setIsAuthenticated(false);
    } finally {
      setLoading(false);
    }
  };

  // Run token validation on mount
  useEffect(() => {
    checkAuth();
  }, []);

  // If loading, render a loading indicator
  if (loading) {
    return <div>Loading...</div>;
  }

  if (isAuthenticated) {
    return <Navigate to="/" replace />;
  } else {
    if (isAuthenticated === null) {
      return <FullScreenLoader />;
    }
  }

  return children; // Render login components if not authenticated
};

NoAuthMiddleware.propTypes = {
  children: PropTypes.node.isRequired,
};

export default NoAuthMiddleware;
